@use '../../../styles/mixins';

.itemCard {
  display: flex;
  flex-direction: column;
  row-gap: 15px;

  margin-bottom: 25px;

  @include mixins.mq(tablet) {
    margin-bottom: 0;
  }
}

.photoLink {
  position: relative;
  overflow: hidden;

  width: 100%;
  height: 440px;

  @include mixins.mq(desktop) {
    height: 300px;
  }
}

.photoCard {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.overlayLabel {
  position: absolute;
  top: 16px;
  left: 16px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  pointer-events: none;

  @include mixins.mq(desktop) {
    top: 6px;
    left: 6px;
  }
}

.label {
  width: 50px;
  margin-bottom: 4px;
  padding: 4px 12px;

  text-align: center;
  border-radius: 18px;
  background-color: #000000;
}

.label > span {
  display: inline-block;

  font-size: 14px;
  line-height: 1.2;
  text-transform: uppercase;

  color: var(--accent-color);
}

.styleDescriptionProductCardBox {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;

  background-color: var(--bg-footer-header);
  opacity: 0.8;
  // text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);

  transform: translateY(101%);
  transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1);

  .photoLink:hover &,
  .photoLink:focus & {
    transform: translateY(0%);
  }
}

.styleDescriptionProductCard {
  position: absolute;
  top: 60px;

  text-align: center;

  height: 60%;
  width: 100%;
  padding: 22px;

  transform: translateY(135%);
  transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1);

  .photoLink:hover &,
  .photoLink:focus & {
    transform: translateY(0%);
  }
}

.descriptionProductCard {
  white-space: pre-wrap;
  display: block;
  position: relative;
  // z-index: 1;
  // max-height: calc(1em * 1.2 * 7);
  overflow: hidden;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  text-decoration: none;

  color: var(--btn-text-color);

  // text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
}

.stylePriceLike {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.stylePrice {
  display: flex;
  align-items: center;
}

.oldPriceCard {
  padding-right: 10px;

  font-weight: 700;
  font-size: 13px;
  line-height: 1.2;
  text-decoration: line-through;

  color: #acadac;
}

.newPriceCard {
  font-weight: 700;
  font-size: 18px;
  line-height: 1.2;

  color: var(--text-color);
}

.priceCard {
  font-weight: 700;
  font-size: 18px;
  line-height: 1.2;

  color: var(--title-text-color);
}

.styleLike {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.liked {
  stroke: var(--icons-color);
  transition: all 0.3s ease-in-out;
}

.active {
  fill: var(--accent-color);
}

.likeCard {
  margin-right: 5px;

  font-size: 12px;
  line-height: 1.2;

  color: var(--second-text-color);
}

.styleSizeCard {
  position: relative;

  display: flex;
  justify-content: left;
  align-items: center;

  margin-top: auto;
  margin-bottom: 0px;
}

.separator {
  margin-left: 2px;
  margin-right: 2px;

  color: var(--second-text-color);
}

.sizeName {
  font-weight: 200;
  font-size: 16px;
  color: var(--text-color);
  margin-bottom: 5px;
  text-align: center;
}

.hoveredSize {
  position: absolute;
  top: 100%;
  left: 0;
  // width: 100%;
  margin-top: 5px;
  background-color: #fff;
  box-shadow: var(--hover-box-shadow);
  z-index: 999;
}

